body{
    font-family:system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif ;
}
h1,h2,h3,h4,h5,h6{
    text-transform: capitalize;
}
main{
    background-color: #f0eaea;
    width: 70%;
    height:auto;
    margin: 20px auto;
    padding: 5px;
    padding-bottom: 15px;
    text-align: center;
}
.underline{
    width:20%;
    height:3px;
    margin: -10px auto 5px auto;
    background-color: rgb(8, 153, 215);
}
.review{
    width: 70%;
    margin: 0 auto;
    background-color: white;
    box-shadow: 0px 1px 5px rgb(175, 169, 169);
    padding: 10px 0;
}
img{
    width: 50%;
    height:120px;
    border-radius: 50%;
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;
    box-shadow: 2px 2px 2px 2px rgb(96, 156, 213);
}
.quote-icon{
    color: white;
    padding: 15px;
    padding-bottom: 10px;
    font-size: 14px;
    border-radius: 50%;
    background-color:  rgb(96, 156, 213);
    position: absolute;
    top: 16%;
    left:35%;
}
h4.author{
    font-size: 12px;
    margin-top: 4px;
}
p.job{
    text-transform: uppercase;
    font-size: 12px;
    margin-top: -16px;
    letter-spacing: -1px;
    color:  rgb(96, 156, 213);
    font-weight: bold;
}
p.info{
    margin: 0 0 10px 0;
    padding: 0px 5px;
    color: #555;
}
button{
    /* border: 1px solid rgb(141, 39, 39); */
    border:0px;
    background-color: inherit;
    padding: 5px 10px 5px 10px;
    color: rgb(96, 156, 213);
    font-weight: bolder;
    margin-bottom: 10px;
    transition: background-color 1s,
                color 1s;
}
button:hover{
    /* border-color: red; */
    color: rgb(15, 116, 210);;
}
button:active{
    /* border-color: pink; */
    color: rgb(174, 202, 228);
}
.prev-btn:active, .next-btn:active{
    border: 1px solid rgb(96, 156, 213);
    padding:0px;
    margin: 0 9px;
}
.random-btn{
    background-color: rgb(236, 246, 254);
    transition: background-color 1s,
                color 0.5s;
}
.random-btn:hover{
    background-color: rgb(34, 98, 159);
    color:white;
}
.random-btn:active{
    background-color: rgb(103, 137, 169);
    color:black;
}
